.header {
    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: center; }
    &__icon {
        display: block;
        width: 292px;
        height: 39.71px;
        margin-top: 31px; }
    &__descr {
        font-weight: 500;
        font-size: 17px;
        color: #5F5F5F;
        margin-top: 1.29px; }
    &__watch {
        font-weight: 500;
        font-size: 18px;
        color: $second;
        margin: 15px 0 0 66px; }
    &__search {
        position: relative;
        margin: 0 0 0 71px; }
    &__find {
        background-color: $input;
        min-width: 400px;
        min-height: 54px;
        border-radius: 30px;
        padding-left: 22px; }
    &__glass {
        position: absolute;
        top: 15px;
        right: 25px; }
    &__choise {
        display: flex;
        align-items: center; }
    &__price {
        width: 54px;
        height: 54px;
        background: #EFEFEF;
        border-radius: 100%;
        margin: 41px 23px 41px 42px;
        padding: 15px 17px 17px 17px; }
    &__likes {
        width: 54px;
        height: 54px;
        background: #EFEFEF;
        border-radius: 100%;
        padding: 10px 4px 14px 17px;
        img {
            width: 30px;
            height: 29px; } }
    &__phone {
        margin-left: 71px;
        a {
            font-size: 24px;
            font-weight: 750;
            color: $second; }
        span {
            color: $main-color; } }

    &__bottom {
        background-color: $second;
        height: 83px; }
    &__bottom-wrapper {
        display: flex;
        justify-content: space-between; }
    &__nav-menu {
        display: flex;
        justify-content: space-between; }
    .dropbtn {
        cursor: pointer;
        background: #535353;
        border-radius: 5px;
        width: 262px;
        height: 65px;
        display: flex;
        align-items: center;
        margin: 9px 69px 9px 0;
        transition: 0.3s all;
        border: 0;
        &:hover {
            background-color: grey;
            color: #FFFFFF; }
        img {
            margin: 0 7px 0 44px; } }
    &__btn-descr {
        font-weight: 500;
        font-size: 18px;
        margin-top: 2.5px; }

    &__nav-list {
        display: flex;
        justify-content: space-between;
        a {
            color: #8E8E8E;
            font-weight: 500;
            font-size: 18px; } }
    &__nav-item {
        margin: 28px 0 28px 0;
        not(:last-child) {}
        margin-right: 100px; }

    &__nav-link {
        &:hover {
            color: #fff; } }

    .dropdown {
        position: relative;
        display: inline-block; }

    .dropdown-content {
        display: none;
        position: absolute;
        background: #535353;
        min-width: 262px;
        min-height: 357px;
        z-index: 1;
        border-radius: 5px;
        position: absolute;
        top: 75px;
        left: 0; }

    .dropdown-content a {
        padding: 10px 0 10px 44px;
        color: #8E8E8E;
        font-weight: 500;
        font-size: 17px;
        display: block;
        // position: relative
        text-align: start;
        &:hover {
            color: #FFFFFF; } }

    .dropdown:hover .dropdown-content {
        display: block; } }

hr {
    color: #606060;
    border: 1.3px solid #606060;
    width: 259px; }
