.footer {
    &__top {
        min-width: 1920px;
        min-height: 285px;
        background: #3E3E3E;
        // padding: 57px 0 0 0
        &-wrapper {
            display: flex; }
        &-logo {
            margin: 57px 44px 0 0; }
        &-title {
            font-style: italic;
            font-weight: 600;
            font-size: 30px;
            color: #FFFFFF; }
        &-descr {
            color: #5F5F5F;
            margin: 2px 0 18px 0;
            font-weight: 500;
            font-size: 18px; }
        &-payment {
            display: flex;
            &-subtitle {
                color: #929292;
                font-weight: 300;
                font-size: 15px;
                margin-right: 16px; }
            &-link {
                margin-right: 19px; } }
        &-catalog {
            margin: 57px 50px 0 50px;
            &-title {
                font-weight: 600;
                font-size: 22px;
                color: #FFFFFF; }
            &-items {
                display: flex;
                justify-content: space-between;
                &-col {
                    display: flex;
                    flex-direction: column;
                    &:not(:last-child) {
                        margin-right: 54px; } } }
            &-link {
                color: #929292;
                font-weight: 300;
                font-size: 15px;
                margin-top: 7px; } }
        &-popular {
            margin: 54px 50px 0 50px;
            &-title {
                font-weight: 600;
                font-size: 22px;
                color: #FFFFFF; }
            &-items {
                display: flex;
                flex-direction: column; }
            &-link {
                color: #929292;
                font-weight: 300;
                font-size: 15px;
                margin-top: 7px; } }
        &-network {
            margin: 57px 59px 0 52px;
            &-title {
                font-weight: 600;
                font-size: 22px;
                color: #FFFFFF;
                margin-bottom: 6px; }
            &-link {
                margin-right: 24px; } }
        &-contacts {
            margin: 54px 0 0 50px;
            &-title {
                color: #FFFFFF;
                font-weight: 600;
                font-size: 22px;
                margin-bottom: 2px; }
            &-call {
                display: flex;
                &-link {
                    margin-left: 9px;
                    font-weight: 300;
                    color: #929292;
                    font-size: 15px; } }
            &-info {
                display: flex;
                margin-top: 13px;
                &-link {
                    margin-left: 10px;
                    font-weight: 300;
                    color: #929292;
                    font-size: 15px; } }
            &-time {
                margin-top: 7px;
                &-item {
                    color: #929292;
                    font-weight: 300;
                    font-size: 15px; } }
            &-map {
                a {
                    font-weight: 600;
                    font-size: 15px;
                    text-decoration-line: underline;
                    color: #929292; } } }
        &-ellipse {
            background: #5E5E5E;
            width: 22px;
            height: 22px;
            border-radius: 100%;
            display: flex;
            justify-content: center;
            align-items: center; } }
    &__bottom {
        height: 49px;
        background: #363434;
        padding: 17px 0 12px 0;
        &-wrapper {
            display: flex;
 }            // justify-content: space-between
        &-policy {
            font-weight: 400;
            font-size: 13px;
            color: #808080;
            margin-right: 443px; }
        &-stores {
            font-weight: 400;
            font-size: 13px;
            color: #808080;
            margin-right: 541px; }
        &-development {
            font-weight: 400;
            font-size: 13px;
            color: #808080; } }

    &__line {
        margin-top: 33px;
        border: 1.5px solid #494949;
        color: #494949;
        height: 208px; } }
