.promo {
    &__slider {
        margin-top: 36px;
        border-radius: 30px;
        height: 593px;
        background: #FF5E5E; }
    &__wrapper {
        display: flex;
        justify-content: space-between; }
    &__title {
        margin: 68px 0 29px 0;
        color: #3E3E3E;
        font-weight: 600;
        font-size: 30px; }
    &__arrows {
        margin: 57px 0 25px 0;
        display: flex;
        &__prev {
            margin-right: 20px;
            width: 54px;
            height: 54px;
            border-radius: 100%;
            background: #FFFFFF;
            display: flex;
            justify-content: center;
            align-items: center; }
        &__next {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 54px;
            height: 54px;
            border-radius: 100%;
            background: #FFFFFF; } }

    &__cards-top {
        display: flex;
        &__img-wrapper {
            margin: 40px 68px 40px 70px;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                width: 296px;
                height: 307px; } }
        &__img-wrap {
            margin: 16px 68px 17px 70px;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                width: 296px;
                height: 307px; } } }
    &__catalog-cards {
        background: #FFFFFF;
        max-width: 408px;
        min-height: 578px;
        border-radius: 30px; }

    &__catalog-cards:not(:last-child) {
        margin-right: 30px; }

    &__catalog-cards {
 }        // padding: 0 0 43px 0

    &__cards-title {
        text-align: center;
        font-weight: 600;
        font-size: 22px;
        color: #3E3E3E; }

    &__cards-subtitle {
        text-align: center;
        font-weight: 400;
        font-size: 16px; }

    &__btn {
        cursor: pointer;
        background: #FF5E5E;
        color: #fff;
        border-radius: 50px;
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.02);
        border: none;
        width: 249px;
        height: 55px;
        margin: 23px 80px 43px 80px; }

    &__new {
        margin: 72px 0 29px 0;
        color: #3E3E3E;
        font-weight: 600;
        font-size: 30px; }

    &__cards-bottom {
        display: flex; }

    &__top-line {
        display: flex;
        justify-content: space-between; }

    &__sale {
        background: linear-gradient(95.9deg, #FB4141 0%, #FBA541 100%);
        width: 84px;
        height: 27px;
        border-radius: 30px;
        text-align: center;
        margin: 32px 0 0 35px;
        color: #FFFFFF;
        font-weight: 600;
        font-size: 15px; }

    &__proceeds {
        margin: 32px 0 0 35px;
        text-align: center;
        color: #3E3E3E;
        font-weight: 600;
        font-size: 15px;
        width: 84px;
        height: 27px;
        background: #FBE341;
        border-radius: 30px; }

    &__heart {
        margin: 28px 33px 0 0;
        font-size: 23px;
        color: grey;
        i {} }

    &__heart {
        i {
            cursor: pointer; } }
    &__dislike {
        text-align: right;
        margin: 30px 33px 0 0; }

    &__brand-title {
        text-align: center;
        font-weight: 600;
        font-size: 22px;
        color: #3E3E3E; }

    &__brand-subtitle {
        text-align: center;
        font-weight: 400;
        font-size: 16px;
        color: #3E3E3E; }

    &__bottom-line {
        display: flex;
        justify-content: space-between; }

    &__price {
        margin: 30px 0 0 35px;
        font-weight: 700;
        font-size: 28px;
        color: #3E3E3E; }

    &__old-price {
        margin-left: 35px;
        color: #C0BFBF;
        font-size: 14px;
        font-weight: 400; }

    &__basket {
        width: 90px;
        height: 44px;
        background: linear-gradient(95.65deg, #FF5E5E 0%, #FF8080 100%);
        box-shadow: 0px 4px 15px rgba(255, 119, 119, 0.5);
        border-radius: 50px;
        padding: 12px 34px 12px 34px;
        cursor: pointer;
        margin: 38px 33px 40px 0; }

    &__notice {
        cursor: pointer;
        width: 90px;
        height: 44px;
        border-radius: 50px;
        background: linear-gradient(95.65deg, rgba(255, 217, 217, 0.99) 0%, #FFD1D1 100%);
        box-shadow: 0px 4px 15px rgba(255, 119, 119, 0.15);
        padding: 10px 34px 9px 34px;
        margin: 37px 34px 0 0; }

    &__stores {
        font-weight: 600;
        font-size: 30px;
        margin: 93px 0 29px 0; }

    &__location {
        height: 608px;
        margin-bottom: 142px; } }
